  }
  
  .sc--twitter {
    background-color: #108ac1;
    background-image: -webkit-linear-gradient(to bottom, #47bbf0, #108ac1 100%);
    background-image: -o-linear-gradient(to bottom, #47bbf0, #108ac1);
    background-image: -webkit-gradient(
      linear,
