  }
  .sc--twitter:hover {
    background-color: #0f83b8;
    background-image: -webkit-linear-gradient(to bottom, #76cdf4, #0f83b8 90%);
    background-image: -o-linear-gradient(to bottom, #76cdf4, #0f83b8);
    background-image: -webkit-gradient(
