  }
  .sc--twitter:active {
    background-color: #108ac1;
    background-image: -webkit-linear-gradient(to bottom, #30b3ee, #108ac1 100%);
    background-image: -o-linear-gradient(to bottom, #30b3ee, #108ac1);
