@import url(https://fonts.googleapis.com/css?family=Heebo:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Components */


.invisible {
  opacity: 0;
  -webkit-transition: 0.7s;
  transition: 0.7s;
}

.fakefield {
  display: none;
}

.left-align {
  text-align: left;
}

.center,
center-align {
  text-align: center;
}

.right-align {
  text-align: right;
}

.stress {
  color: #111;
}


img,
svg {
  max-width: 100%;
}

.container {
  width: 88%;
  max-width: 1080px;
  margin: 0 auto;
  position: relative;
}

.row {
  display: block;
  width: 100%;
  clear: both;
}

.half {
  width: 50%;
  overflow: auto;
  float: left;
}

.text-container {
  max-width: 640px;
}
.text-container--center {
  margin-left: auto;
  margin-right: auto;
}


.button {
  display: inline-flex;
  align-items: center;
  padding: 6pt 20pt;
  line-height: 1.9;
  border: 1px solid #165b98;
  color: #165b98;
  font-weight: 400;
  white-space: nowrap;
  cursor: pointer;
  background-color: transparent;
  border-radius: 5px;
  margin-bottom: 1.3em;
  -webkit-transition: all 0.7s, visibility 0s;
  transition: all 0.7s, visibility 0s;
  text-align: center;
  text-decoration: none;
}
button:hover,
button:focus,
.button:hover,
.button:focus {
  outline: 0;
}
@media (min-width: 992px) {
  .button:hover {
    border-color: #463aff;
    color: #463aff;
  }
}

.button > svg:first-child {
  margin-right: 15px;
}

.button__primary {
  background-color: #631ce9;
  border-color: #631ce9;
  color: #fff;
}
@media (min-width: 992px) {
  .button__primary:hover {
    background-color: #463aff;
    border-color: #463aff;
    color: #fff;
  }
}

.button__tertiary {
  background-color: transparent;
  border-color: transparent;
  color: #8e8d8d;
  font-weight: bold;
  text-decoration: underline;
}
@media (min-width: 992px) {
  .button__tertiary:hover {
    border-color: transparent;
    color: #8e8d8d;
  }
}

.button__accent {
  background-color: #165b98;
  border-color: #165b98;
  color: #fff;
}
@media (min-width: 992px) {
  .button__accent:hover {
    background-color: #165b98;
    border-color: #165b98;
    color: #fff;
  }
}

.button__delete {
  background-color: tomato;
  border-color: tomato;
  color: #fff;
}
@media (min-width: 992px) {
  .button__delete:hover {
    background-color: #ff5233;
    border-color: #ff5233;
    color: #fff;
  }
}

.button:disabled {
  opacity: 0.25;
  cursor: default;
  pointer-events: none;
}

.button--fixed {
  width: 160px;
}

.button--fixed2 {
  width: 220px;
}

/* Icon button */
.icon-button {
  color: inherit;
  background-color: transparent;
  border: 0;
  padding: 0;
  cursor: pointer;
}
.icon-button svg {
  vertical-align: middle;
}

input[type="email"],
/*input[type="number"],*/
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 1px solid #eee;
  width: 100%;
  -webkit-transition: 0.7s;
  transition: 0.7s;
  color: #111;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  border-radius: 3px;
  padding: 10px;
  background-color: #f7f7f7;
  margin-bottom: 1.4em;
  display: block;
}
input[type="email"]:focus,
input[type="number"]:focus,
input[type="search"]:focus,
input[type="text"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
input[type="password"]:focus {
  border-color: rgba(108, 99, 255, 0.5);
  outline: 0;
  background-color: #ffffff;
}

textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 1px solid #eee;
  width: 100%;
  -webkit-transition: 0.7s;
  transition: 0.7s;
  color: #111;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  border-radius: 3px;
  padding: 12px 10px;
  background-color: #f7f7f7;
  margin-bottom: 1.4em;
  display: block;
  resize: none;
  min-height: 6em;
  outline: 0;
}
textarea:focus {
  border-color: rgba(108, 99, 255, 0.5);
  outline: 0;
  background-color: #ffffff;
}

.icon__label {
  text-align: left;
  padding: 7pt 3pt;
  display: flex;
  align-items: flex-start;
  line-height: 1.8;
}
.icon__label.small {
  line-height: 1;
}
.icon__label > div:first-child,
.icon__label > svg:first-child {
  margin-right: 10px;
  flex-shrink: 0;
}
.icon__label > span:nth-child(2) {
  line-height: 1.3;
  color: #666;
}

small.error,
label.error {
  color: red;
  padding: 0;
}
input.error,
textarea.error {
  border-color: #faa7bb;
  background-color: #fdf4f5;
}
small.success,
label.success {
  color: green;
  padding: 0;
}

.landing__section {
  padding: 24pt 0;
}

.landing__section:nth-child(odd) {
  background-color: #f7f7f7;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
}

.navbar--home {
  position: fixed;
  z-index: 100;
  width: 100%;
  background-color: #165b98;
  -webkit-transition: 0.7s;
  transition: 0.7s;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.07), 0 1px 5px 0 rgba(0, 0, 0, 0.11),
    0 3px 1px -2px rgba(0, 0, 0, 0.1);
}
.navbar--nofixed {
  position: relative;
  z-index: 3;
}


.navbar__inner {
  display: flex;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  /*  justify-content: space-between;  */
  height: 60px;
  color: #fff;
  -webkit-transition: 0.7s;
  transition: 0.7s;

  margin-left:-75px;
}

.navbar__logo {
  width: 180px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 320px) {
  .navbar__logo {
    width: 140px;
  }
}
@media (min-width: 321px) and (max-width: 400px) {
  .navbar__logo {
    width: 160px;
  }
}

.navbar__menu {
  display: none;
  font-size: 16px;
  font-weight: 600;
  padding-top: 20px;
}
.navbar__menu--noMob {
  display: block;
}
@media (min-width: 992px) {
  .navbar__menu {
    display: block;
  }
}
.navbar__menu li {
  margin-left: 4pt;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  display: inline-block;
}
@media (min-width: 992px) {
  .navbar__menu li {
    margin-left: 16pt;
  }
}
.navbar__menu a {
  color: #ffffff;
}
.navbar__menu a svg,
.nav__mobile a svg {
  fill: #165b98;
  width: 12px;
  height: 12px;
}
@media (min-width: 992px) {
  .navbar__menu a {
    opacity: 0.75;
  }
  .navbar__menu a:hover {
    opacity: 1;
  }
  .navbar__menu a:after {
    display: block;
    content: "";
    border-bottom: solid 1px #165b98;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: -webkit-transform 250ms ease-in-out;
    transition: -webkit-transform 250ms ease-in-out;
    transition: transform 250ms ease-in-out;
    transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
  }
  .navbar__menu a:hover:after {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}

.navbar__menu-mob {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end;
}
.navbar__menu-mob > .covid-19 {
  flex-grow: 1;
  text-align: center;
  color: inherit;
}
.navbar__menu-mob > .icon-button {
  margin-left: 10px;
}
.navbar__menu-mob--noMob {
  display: none;
}
@media (min-width: 992px) {
  .navbar__menu-mob {
    display: none;
  }
}

/*--- Extended and transparent version ---*/
.navbar--extended {
  background-color: transparent;
  box-shadow: none;
}
.navbar--extended .navbar__inner {
  height: 60px;
  color: #ffffff;
}

.navbar--extended a {
  color: #165b98;
}
.nav__mobile {
  -webkit-transition: max-height 284ms ease 0s;
  transition: max-height 284ms ease 0s;
  clip: rect(0 0 0 0);
  max-height: 0;
  display: block;
  overflow: hidden;
  zoom: 1;
}

.nav__mobile ul {
  margin: 0;
  padding: 0;
  width: 100%;
  display: block;
  list-style: none;
  background-color: #333c44;
  color: #fff;
}

.nav__mobile li a {
  color: #fff;
}

.nav__mobile li {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  padding: 1em 0;
  display: block;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.nav__mobile li:last-child {
  border-bottom: none;
}
.nav__mobile li a {
  display: block;
}

.nav-toggle {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

@media (min-width: 992px) {
  .js .nav__mobile {
    position: relative;
  }
  .js .nav__mobile.closed {
    max-height: none;
  }
  .nav-toggle {
    display: none;
  }
}

.hero {
  position: relative;
  width: 100%;
  background-image: url(/bon-munch/static/media/motorhome-background-small.fefa5aa9.jpg);
  box-shadow: 0 4px 11px rgba(124, 146, 169, 0.5);
  background-size: cover;
  background-position: right center;
  background-repeat: no-repeat;
  color: #ffffff;
}
@media (min-width: 768px) {
  .hero {
    background-image: url(/bon-munch/static/media/motorhome-background-big.74cc987a.jpg);
  }
}
@media (min-width: 1200px) {
  .hero {
    background-position: bottom center;
  }
}

.hero__inner {
  position: relative;
  z-index: 2;
}

.hero__overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0.36;
  z-index: 1;
  background-color: rgba(108, 99, 255, 0.5);
}

.hero__overlay--gradient {
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(108, 99, 255, 0.5)),
    to(rgba(108, 99, 255, 0.6))
  );
  background: linear-gradient(
    to right,
    rgba(108, 99, 255, 0.5),
    rgba(108, 99, 255, 0.6)
  );
}

.hero__overlay--gradientVertical {
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(0, 0, 0, 0.4)),
    to(rgba(0, 0, 0, 0.6))
  );
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.4),
    rgba(0, 0, 0, 0.6)
  );
}

.hero__content {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 50px);
  padding: 92px 0;
  overflow: auto;
}
.hero__content__inner {
  width: 100%;
  max-width: 600px;
  text-align: center;
}

.bonMunch-card {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 2px 6px 0 hsl(0, 0%, 0%, 0.2);
}

/*--- CONTENT STYLING ---*/
.hero__title,
.hero__subTitle {
  color: inherit;
  margin-bottom: 1.25em;
  font-weight: bold;
  text-shadow: 0 0 50px hsla(0, 0%, 0%, 0.7);
}

.hero__subTitle {
  margin-bottom: 0.5em;
}

.hero__text {
  line-height: 1.7;
  text-align: left;
  font-size: 1em;
  text-shadow: 0 0 20px hsla(0, 0%, 0%, 1);
}

.hero p a {
  border-bottom: 1px dotted #fff;
}

.hero .button {
  margin: 12pt 8pt;
}

.hero__button {
  color: #ffffff;
  border-color: #ffffff;
}
@media (min-width: 992px) {
  .hero__button:hover {
    color: #e3e3e3;
    border-color: #e3e3e3;
  }
}

.hero__sub {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
}

.hero__sub__down {
  width: 1rem;
  height: 1rem;
  color: #666;
  -webkit-transition: 0.7s;
  transition: 0.7s;
  cursor: pointer;
  /* animation: pulse 3s ease-in-out infinite; */
  -webkit-animation: bounce 2s linear infinite;
  animation: bounce 2s linear infinite;
  border-radius: 50%;
}
@media (min-width: 992px) {
  .hero__sub__down:hover {
    color: #6c63ff;
  }
}

@-webkit-keyframes pulse {
  0%,
  70% {
    box-shadow: 0px 0px 0px 0px rgba(108, 99, 255, 0.5);
  }
  100% {
    box-shadow: 0px 0px 0px 50px rgba(108, 99, 255, 0);
  }
}

@keyframes pulse {
  0%,
  70% {
    box-shadow: 0px 0px 0px 0px rgba(108, 99, 255, 0.5);
  }
  100% {
    box-shadow: 0px 0px 0px 50px rgba(108, 99, 255, 0);
  }
}

@-webkit-keyframes bounce {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  20% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }
  40% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes bounce {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  20% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px);
  }
  40% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

#scrollToNext {
  -webkit-transition: 0.7s;
  transition: 0.7s;
}

/*--- MODIFICATIONS ---*/
.hero--full .hero__content {
  min-height: 100vh;
}

.hero--full + .hero__sub {
  display: none;
}

.steps {
  text-align: center;
  padding: 16pt 0;
}

@media (min-width: 992px) {
  .steps__inner {
    display: flex;
    justify-content: space-around;
  }
}
@media (min-width: 1025px) {
  .steps__inner {
    flex-wrap: wrap;
  }
}

.step {
  padding: 0 0 16pt 0;
  text-align: center;
  width: 100%;
  border-top: 1px solid #eee;
  overflow: auto;
}
.step:first-child {
  border-top: none;
}
@media (min-width: 992px) {
  .step {
    border-top: none;
    width: auto;
  }
}

.step__media {
  margin: 16pt 0;
}
@media (min-width: 992px) {
  .step__media {
    margin: 32pt 0;
  }
}

.step__image {
  max-height: 100px;
  max-width: 222px;
  margin-top: 8pt;
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}
@media (min-width: 600px) {
  .step__image {
    max-width: none;
  }
}

.step__text {
  max-width: 36ch;
  text-align: left;
  margin-left: auto;
  margin-right: auto;
}

.steps__more__info > .button > span {
  min-width: 20px;
  display: inline-block;
}

.steps__more__info__content {
  -webkit-transition: max-height 500ms ease-in-out;
  transition: max-height 500ms ease-in-out;
  max-height: 0;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  max-width: 36ch;
  margin: 0 auto 20px;
}
@media (min-width: 600px) {
  .steps__more__info__content {
    max-width: 70ch;
  }
}
@media (min-width: 992px) {
  .steps__more__info__content {
    max-width: none;
  }
}

.steps__more__info__content.content__expanded {
  max-height: 1000px;
}

.steps__more__info__content .reason {
  width: 100%;
}
@media (min-width: 600px) {
  .steps__more__info__content .reason {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .steps__more__info__content .reason {
    width: 33.33%;
  }
}

.expanded {
  padding: 72pt 0;
}

.expanded__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 992px) {
  .expanded__inner {
    flex-direction: row;
  }
  .expanded__inner.expanded__inner__one_col {
    flex-direction: column;
  }
}

.expanded__media {
  order: 1;
  text-align: center;
}
@media (min-width: 992px) {
  .expanded__media {
    width: 50%;
    flex-shrink: 0;
    order: unset;
  }
}

.expanded__content {
  order: 2;
}
@media (min-width: 992px) {
  .expanded__content {
    padding: 0 16pt;
    width: 50%;
    flex-shrink: 0;
    order: unset;
  }
}

.expanded:nth-child(even) .expanded__media {
  order: 1;
}
@media (min-width: 992px) {
  .expanded:nth-child(even) .expanded__media {
    order: unset;
  }
}

/* Actual content */
.expanded__image {
  width: 90%;
  max-width: 220px;
  padding: 16pt 0;
}
@media (min-width: 992px) {
  .expanded__image {
    max-width: 280px;
  }
}

.expanded__title {
  max-width: 70ch;
}

.expanded__text {
  max-width: 70ch;
}

.cta {
  background: -webkit-gradient(linear, left top, right top, from(#6c63ff), to(#4641ff));
  background: linear-gradient(to right, #6c63ff, #4641ff);
  color: #fff;
  padding: 48pt 0;
  text-align: center;
}cta--reverse {
  background: -webkit-gradient(linear, right top, left top, from(#6c63ff), to(#4641ff));
  background: linear-gradient(to left, #6c63ff, #4641ff);
}

.cta--plain {
  background: #6c63ff;
}

.cta--secondary {
  background: #4641ff;
}

.cta__title {
  color: inherit;
}

.cta__sub {
  opacity: 0.85;
  max-width: 58ch;
  margin: 16pt auto 24pt auto;
}
.cta__sub--center {
  text-align: center;
}

.footer {
  background-color: #ffffff;
  padding: 36pt 0;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
@media (min-width: 600px) {
  .footer__inner {
    flex-direction: row;
  }
}

.footer__logo {
  margin-bottom: 1.5rem;
}
@media (min-width: 600px) {
  .footer__logo {
    margin-bottom: 0;
    flex-basis: 60%;
    margin-right: 4rem;
  }
}

.footer__logo > span {
  font-size: 0.8rem;
}

.footer__textLogo {
  max-width: 170px;
  min-width: 33%;
  flex-shrink: 0;
  padding-right: 16pt;
  display: block;
  cursor: pointer;
  color: #6c63ff;
  font-size: 1.225rem;
}
@media (min-width: 992px) {
  .footer__textLogo {
    min-width: 25%;
    font-size: 1.325rem;
  }
}

.footer__data {
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  padding-top: 0.25rem;
  flex-wrap: wrap;
  font-size: 0.9rem;
}
@media (min-width: 992px) {
  .footer__data {
    flex-wrap: nowrap;
    flex-direction: row;
  }
}

.footer__data__item {
  padding-bottom: 0.5rem;
  display: inline-block;
  margin-bottom: 8pt;
}

.footer__social {
  margin-right: 2.5em;
}
@media (min-width: 992px) {
  .footer__pages,
  .footer__social {
    margin-right: 4em;
  }
  .footer__pages {
    min-width: 180px;
  }
}

.footer__link {
  color: #6c63ff;
  border-bottom: 1px dotted #6c63ff;
  padding-bottom: 1pt;
  -webkit-transition: 0.7s;
  transition: 0.7s;
}
@media (min-width: 992px) {
  .footer__link:hover {
    color: #fafafa;
    border-color: #fafafa;
  }
}

.footer__row {
  margin-bottom: 8pt;
}

/*--- Modifiers---*/
.footer--dark {
  background-color: #165b98;
}
.footer--dark .footer__textLogo {
  color: #fff;
}
.footer--dark .footer__inner {
  color: #d9d9d9;
}
.footer--dark .footer__link {
  color: #d9d9d9;
  border-color: #d9d9d9;
}
@media (min-width: 992px) {
  .footer--dark .footer__link:hover {
    color: #fff;
    border-color: #fff;
  }
}

.auth {
  padding-top: 102.66667px;
}

.auth__inner {
