@-webkit-keyframes ring {
  /* start from original */
  0% {
  }
  /* rotate right */
  20% {
    -webkit-transform: rotate(10deg);
    transform: rotate(10deg);
  }
  /* rotate left */
  40% {
    -webkit-transform: rotate(-10deg);
    transform: rotate(-10deg);
  }
  /* rotate right */
  60% {
    -webkit-transform: rotate(10deg);
    transform: rotate(10deg);
  }
  /* return center */
  80% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

@keyframes ring {
  /* start from original */
  0% {
  }
  /* rotate right */
  20% {
    -webkit-transform: rotate(10deg);
    transform: rotate(10deg);
  }
  /* rotate left */
  40% {
    -webkit-transform: rotate(-10deg);
    transform: rotate(-10deg);
  }
  /* rotate right */
  60% {
    -webkit-transform: rotate(10deg);
    transform: rotate(10deg);
  }
  /* return center */
  80% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

